@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;1,400;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;1,400;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Epilogue:wght@900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;1,400;1,700&display=swap);
input {
  border: 1px solid rgb(167, 167, 167);
  border-radius: 5px;
  color: #323333;
  font-family: Lato, sans-serif;
  font-size: 16px;
  padding: 8px;
}

@media ( min-width : 760px ) {

  input {
    font-size: 18px;
  }
}

.logo {
  align-items: baseline;
  display: flex;
  font-size: 22px;
  font-weight: 300;
  grid-gap: 5px;
  gap: 5px;
  justify-content: center;
  letter-spacing: 1px;
  margin-bottom: 5px;
}

.logo p {
  margin: 0;
}

.logo img {
  height: auto;
  max-width: 100%;
  width: 30px;
}

.trybe {
  font-family: Epilogue, sans-serif;
  font-weight: 900;
  letter-spacing: normal;
}

@media ( min-width : 760px ) {

  .logo {
    font-size: 28px;
    margin-bottom: 25px;
  }

  .logo img {
    width: 38px;
  }

}

body {
  background: linear-gradient(0deg, rgb(3, 99, 76) 0%, rgba(47, 193, 140, 1) 100%);
  height: 100vh;
}

.login-card {
  align-items: center;
  background-color: white;
  border: 1px solid rgba(167, 167, 167, 0.6);
  border-radius: 5px;
  bottom: 0;
  display: flex;
  flex-direction: column;
  grid-gap: 25px;
  gap: 25px;
  height: 300px;
  justify-content: center;
  left: 0;
  margin: auto;
  position: absolute;
  right: 0;
  top: 0;
  width: 250px;
}

button {
  background-color: rgba(47, 193, 140, 1);
  border: none;
  border-radius: 5px;
  color: white;
  font-family: Lato, sans-serif;
  font-size: 16px;
  padding: 8px 25px;
}

button:disabled {
  opacity: 0.4;
}

button:enabled:hover {
  cursor: pointer;
}

@media ( min-width : 760px ) {

  .login-card {
    grid-gap: 30px;
    gap: 30px;
    height: 400px;
    width: 350px;
  }

  button {
    font-size: 18px;
    padding: 10px 28px;
  }
}

.container {
  align-items: center;
  background-color: white;
  display: flex;
  flex-direction: column;
  height: 100vh;
  padding-top: 150px;
  width: 100vw;
}

h2 {
  margin-bottom: 50px;
  margin-top: 150px;
}

a:visited {
  color: #323333;
}

@media ( min-width : 760px ) {

  h2 {
    font-size: 28px;
  }

  a {
    font-size: 20px;
  }
}

header {
  align-items: center;
  background-color: white;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 15px 20px;
}

.user {
  align-self: flex-end;
  display: flex;
  grid-gap: 15px;
  gap: 15px;
  justify-content: center;
}

.user-info {
  display: flex;
  flex-direction: column;
  grid-gap: 5px;
  gap: 5px;
  margin-top: 15px;
}

.user-info p {
  margin: 0;
}

.user-symbol {
  display: none;
}

.expenses {
  display: flex;
  grid-gap: 5px;
  gap: 5px;
  justify-content: flex-end;
}

@media ( min-width : 760px ) {

  header {
    flex-direction: row;
    justify-content: space-between;
    padding-bottom: 30px;
  }

  .user-info {
    align-self: center;
    font-size: 18px;
    margin-top: 0;
    padding-right: 20px;
  }

  .user-symbol {
    color: rgba(47, 193, 140, 1);
    display: block;
    font-size: 40px;
  }

  .logo {
    margin-bottom: 0;
  }
}

th , td {
  min-width: -webkit-fit-content;
  min-width: -moz-fit-content;
  min-width: fit-content;
  width: 11%;
}

th {
  border-bottom: 1px solid #323333;
  padding-bottom: 10px;
  padding-top: 10px;
}

td:nth-child( 4 ) , td:nth-child( 6 ) , td:nth-child( 7 ) {
  text-align: center;
}

tr:nth-child( even ) {
  background-color: rgba(47, 193, 140, 0.15);
}

.action-btns {
  display: flex;
  flex-direction: row-reverse;
  justify-content: center;
  max-width: -webkit-fit-content;
  max-width: -moz-fit-content;
  max-width: fit-content;
}

.action-btns button {
  align-items: center;
  align-self: center;
  background-color: transparent;
  display: flex;
  font-size: 22px;
  height: 30px;
  justify-content: center;
  margin: 0;
  max-width: 100px;
}

.delete-btn {
  color: rgb(223, 30, 30);
}

.edit-btn {
  color: blue;
}

h2 {
  text-align: center;
}

@media ( max-width : 910px ) {

  /* Baseado em https://elvery.net/demo/responsive-tables/ (no more tables) */
  table , thead , tbody , th , td , tr {
    display: block;
  }

  tr {
    border: 1px solid #323333;
  }

  thead tr {
    left: -9999px;
    position: absolute;
    top: -9999px;
  }

  td {
    align-items: center;
    border: none;
    border-bottom: 1px solid  rgba(3, 99, 76, 0.3);
    display: flex;
    height: 50px;
    justify-content: flex-start;
    overflow-wrap: break-word;
    padding-left: 50%;
    position: relative;
    width: auto;
  }

  td:before {
    content: attr(data);
    font-weight: 700;
    left: 6px;
    overflow-wrap: break-word;
    padding-bottom: 10px;
    padding-right: 10px;
    position: absolute;
    text-align: left;
    white-space: normal;
    width: 45%;
  }


  .action-btns button {
    height: 20px;
    width: 100%;
  }

  h2 {
    font-size: 18px;
  }
}

form {
  align-items: center;
  background-color: rgb(3, 99, 76);
  display: flex;
  flex-direction: column;
  grid-gap: 5px;
  gap: 5px;
  justify-content: center;
  padding: 10px;
}

select {
  background-color: white;
  border: 1px solid rgb(167, 167, 167);
  border-radius: 5px;
  color: #323333;
  font-family: Lato, sans-serif;
  font-size: 16px;
  padding: 8px;
  width: 220px;
}

button {
  font-size: 16px;
  margin-top: 15px;
  width: 200px;
}

input {
  width: 202px;
}

@media ( min-width : 600px ) {

  form {
    align-items: center;
    flex-flow: row wrap;
    justify-content: space-evenly;
    padding: 30px 20px;
  }

  button {
    font-size: 18px;
    margin-top: 0;
    width: 220px;
  }

  select {
    font-size: 18px;
  }
}

@media ( min-width : 1062px ) {

  form {
    flex-wrap: nowrap;
    grid-gap: 20px;
    gap: 20px;
    justify-content: flex-start;
  }

  button {
    margin-top: 0;
    width: 200px;
  }
}

.content {
  background-color: white;
  min-height: 100vh;
  width: 100vw;
}

body {
  color: #323333;
  font-family: Lato, sans-serif;
  font-size: 16px;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen,
    Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue,
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, Courier New,
    monospace;
}

